export default {
    data() {
        return {
            wallet_value: '',
            wallet_key: '',
            address_out: '',
            flag: true,
            right: '',
            id: '',
            shade: true,
            bar: '',
            fee: '',
            user_id: ''
        };
    },
    onLoad(options) {
        var that = this;
        that.bar = options.bar;
        that.fee = options.fee;
        uni.request({
            url: this.url + 'walletaddress/',
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success(res) {
                if (res.statusCode == 302) {
                    uni.showToast({
                        title: '资金密码未设置',
                        icon: 'none',
                        duration: 2000
                    });
                }
                if (res.data.data == '') {
                    that.flag = false;
                } else {
                    that.flag = true;
                }
                that.address_out = res.data.data;
            }
        });
    },
    methods: {
        click: function(item) {
            uni.redirectTo({
                url: '../transfer/transfer?wallet_value=' + item.wallet_value + '&bar=' + this.bar + '&fee=' + this.fee
            });
        }
    }
};
